
import React from 'react'
import { Container } from 'react-bootstrap';
import Navbar from 'react-bootstrap/Navbar';
import { Link } from 'react-router-dom';




const myNavbar = () => {
    return (
        <Container> 
            <div className="">
                    <Navbar expand="lg" variant="light">
                        <Navbar.Brand href="#">Spacious</Navbar.Brand>
                    </Navbar>

                        <div className="Sector d-flex gap-1">

                            <div className="">
                                <Link exact to="/"><button type="button" class="btn btn-light bt-lg pdiv" > PLANETS</button></Link>
                            </div>

                            <div className="pdiv2">
                                <Link to="/Planetcharacter"><button type="button" class="btn ">CHARACTERS</button></Link>
                            </div>

                        </div>
             </div>
        </Container>
    )
}

export default myNavbar
