import React from 'react'
import { Container } from 'react-bootstrap';
import InfoModal from './Modal3';
import plan from '../planet-loader.svg';


const Planets = () => {
    const Image = {}

    return (
        
        <Container className="">
            <div className="join d-flex justify-content-center mt-3 ">
                <img src={plan} alt="" />
            </div>

            <div className="word">
                <h2 className="d-flex justify-content-center mt-3 pt-3">Space doesn't have to be so empty. </h2>
            </div>

            <div className="">
                <button type="button" className="btn btn-dark  m-auto d-flex text-uppercase Buttonplan">Create Planet</button>
            </div>
                    <div className="gotomo">
                         <InfoModal/>
                    </div> 
        </Container>
           
    )
}

export default Planets;
