import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Navbar from 'react-bootstrap/Navbar';
import InfoModal from './Modal1';
import nine from '../planet-9.svg';
import eight from '../planet-8.svg';
import seven from '../planet-7.svg';
import six from '../planet-6.svg';
import three from '../planet-3.svg';
import one from '../planet-1.svg';
import two from '../planet-2.svg';
import four from '../planet-4.svg';
import cancel from '../x-square.svg';
import pye from '../square-solid.svg';
import image16 from '../character-16.png';
import image7 from '../character-7.png';
import image3 from '../character-3.png';
import image5 from '../character-5.png';
import image11 from '../character-11.png';
import image6 from '../character-6.png';


const Planetsmain = () => {

    const Planets = [
        { id:1, image: nine, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:2, image: eight, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:3, image: seven, planetName: 'Planet Alpha', pop: 'pop: 235'},

        { id:4, image: six, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:5, image: three, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:6, image: one, planetName: 'Planet Alpha', pop: 'pop: 235'},
       
        { id:7, image: two, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:8, image: four, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:9, image: seven, planetName: 'Planet Alpha', pop: 'pop: 235'},
     
    ];

        
return ( 
    
        <div>
            <Container className="">
                <Row>
                    <div className="yes col-lg-8 col-md-7 d-none d-md-block px-3 mt-5">
                        <div className="">
                            <div className="row">
                                    { Planets.map ( Planet => (
                                    
                                        <div className="col-lg-4 col-md-6 my-3" key={Planet.id}>

                                            <div className="card very border-secondary">
                                                <div className="d-flex justify-content-center High">
                                                <img src={Planet.image} className="" alt=""/>
                                                </div>

                                                <div className="ms-3 mb-2">
                                                    <p className="here ms-2"> {Planet.planetName}</p>
                                                    <p className="here2 m-2">{Planet.pop}</p>
                                                </div>
                                             </div>
                                        </div>
                                    ))}
                            </div>
                            <div className="omo">
                                <InfoModal/>
                            </div>  
                        </div> 
                                   
                    </div>

                <div className="Brace2 col-lg-3 col-md-4  border rounded-3 border-light">
            
                        <div className=" d-flex justify-content-end"> 
                            <img src={cancel} alt="" className="img-fluid plus m-4 bg-light"/>
                        </div>

                        <Navbar expand="lg" variant="light">
                            <Navbar.Brand href="#"  className=" fs-3 mt-5">Planet Alpha</Navbar.Brand>
                        </Navbar>

                        <p className="wordmain">Planet Alpha is the place to be if you like<br/> everything related to planets. I know it’s a<br/> bit meta, but come see by yourself.</p>

                        <p className="Pop">Population</p>
                        <p className="Num">234</p>

                        <div className="d-flex">
                        <p><span className="Chart">CHARACTERS</span> <img src= {pye} alt="" className="img-fluid  plus" /></p>
                        </div>

                        <div className="d-flex gap-4 mt-3">
                            <div>
                            <img src={image16} alt="" className="faces img-fluid border rounded bg-light"/>
                            </div>
                            <div>
                                <h6 className="Names fs-5 mt-2">Darlene Robertson</h6>
                                <p className="wordmain">23 Friends</p>
                            </div>
                        </div>

                        <div className="d-flex gap-4">
                            <div>
                            <img src={image7} alt="" className="faces img-fluid border rounded bg-light"/>
                            </div>
                            <div>
                                <h6 className="Names fs-5 mt-3">Kathryn Murphy</h6>
                                <p className="wordmain">23 Friends</p>
                            </div>
                        </div>

                        <div className="d-flex gap-4">
                            <div>
                            <img src={image3} alt="" className="faces img-fluid border rounded bg-light"/>
                            </div>
                            <div>
                                <h6 className="Names fs-5 mt-3">Jenny Wilson</h6>
                                <p className="wordmain">23 Friends</p>
                            </div>
                        </div>

                        <div className="d-flex gap-4 mt-3">
                            <div>
                            <img src={image5} alt="" className="faces img-fluid border rounded bg-light"/>
                            </div>
                            <div>
                                <h6 className="Names fs-5 mt-3">Arlene McCoy</h6>
                                <p className="wordmain">23 Friends</p>
                            </div>
                        </div>

                            <div className="d-flex gap-4 mt-3">
                                <div>
                                <img src={image11} alt="" className="faces img-fluid border rounded bg-light"/>
                                </div>
                                <div>
                                    <h6 className="Names fs-5 mt-3">Darrell Steward</h6>
                                    <p className="wordmain">23 Friends</p>
                                </div>
                            </div>

                        <div className="d-flex gap-4 mt-3">
                            <div>
                            <img src={image6} alt="" className="faces img-fluid border rounded bg-light"/>
                            </div>
                            <div>
                                <h6 className="Names fs-5 mt-3">Bessie Cooper</h6>
                                <p className="wordmain">23 Friends</p>
                            </div>
                        </div>   
                </div>
                </Row>
            </Container>
        </div>
    )
}

export default Planetsmain;
