import React from 'react'
import InfoModal from './Modal1';
import { Link } from 'react-router-dom';
import nine from '../planet-9.svg';
import eight from '../planet-8.svg';
import seven from '../planet-7.svg';
import six from '../planet-6.svg';
import three from '../planet-3.svg';
import one from '../planet-1.svg';
import two from '../planet-2.svg';
import four from '../planet-4.svg';



const Planetdisplay = () => {


    const Planets = [
        { id:1, image: nine, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:2, image: eight, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:3, image: seven, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:4, image: six, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:5, image: six, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:6, image: three, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:7, image: one, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:8, image: two, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:9, image: two, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:10, image: four, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:11, image: seven, planetName: 'Planet Alpha', pop: 'pop: 235'},
        { id:12, image: three, planetName: 'Planet Alpha', pop: 'pop: 235'},
    ];


    return (
        <div>
            <div className=" mt-5">
                <div className="container">
                    <div className="row">
                    
                            { Planets.map ( Planet => (
                                        
                                <div className="col-12 col-lg-3 col-md-4 my-3" key={Planet.id}>
                                    <Link to="/Planetsmain" className="text-decoration-none">
                                        <div className="magnify card border rounded-3 ">
                                            <div className="d-flex justify-content-center High">
                                                <img src={Planet.image} className="image-fliud" alt=""/>
                                            </div>

                                            <div className=" mb-2">
                                                <p className="here ms-2"> {Planet.planetName}</p>
                                                <p className="here2 m-2">{Planet.pop}</p>
                                            </div>
                                        </div>
                                        </Link>
                                    </div>
                                ))}
                    </div>
                </div>

                <div className="got">
                    <InfoModal/>
                </div>           
            </div>
        </div>
    )
}

export default Planetdisplay;
