import React, {useState} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import {Modal, Button} from 'react-bootstrap';
import { PlusCircleFill } from 'react-bootstrap-icons';
import { Form, InputGroup} from 'react-bootstrap';


export default function InfoModal() {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
        <PlusCircleFill size="60" id="plus-create" onClick={handleShow}/>


        <Modal show={show} onHide={handleClose}  id="planet-modal">

            <Modal.Header closeButton>
                <Modal.Title className="mx-5" id="pm-title">Planet</Modal.Title>
            </Modal.Header>


            <Form className="mx-5">

                <Modal.Body>

                    <Form.Group className="mb-3" controlId="formBasicImage">
                       <Form.Label>Image</Form.Label>
                        <Form.Control type="url" placeholder="" className="bg-light"/>
                       <Form.Text className="text-muted">
                           Paste the URL of a JPG or PNG of max 20kb
                       </Form.Text>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="floatingTextarea">
                        <Form.Label>Name</Form.Label>
                        <Form.Control type="text" placeholder="" className="bg-light"/>
                     </Form.Group>

                     <Form.Group className="mb-3" controlId="floatingTextarea2">
                            <Form.Label>Description</Form.Label>
                            <Form.Control as="text" style={{ height: '100px'}} rows={3} className="bg-light"/>
                            <Form.Text className="text-danger">
                                Bummer! we can't create this planet right now, Probably a black hole in the way.<br/>
                                Try later please.
                            </Form.Text>
                    </Form.Group>

                </Modal.Body>

                    <Modal.Footer>
                        <div className="d-flex justify-content-end my-5 gap-3">
                            <Button variant="secondary" onClick={handleClose}>
                                CANCEL
                            </Button>
                            <Button variant="primary" onClick={handleClose}>
                                CREATE PLANET
                            </Button>
                        </div>
                    </Modal.Footer>
            </Form>
        </Modal>
    </>
  );
}